<template>
  <div class="improvementFile">
    <div class="tableTop">
      <div class="tableTitle"><crumb></crumb></div>
      <el-select v-model="model" placeholder="文件类型">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </div>
    <div class="table">
      <el-table
        :data="tableData"
        style="width: 100%">
         <el-table-column type="expand">
          <template slot-scope="props">
            <div style="overflow:hidden;">
              <span style="float:left;font-weight:bold;font-size:14px;line-height: 30px">其他附件：</span>
              <div style="float:left;overflow:hidden;padding-left: 20px;" v-for="(item, index) in props.row.other" :key="index">
                <div style="float:left; margin: 0 50px 20px 0;overflow: hidden">
                  <div style="border:1px solid rgba(221,221,221,1);border-radius:5px;padding: 5px 10px; float:left;background:#eee">{{item.fileName}}</div>
                  <svgIcon icon-class="iconxiazai" style="margin-left:10px;float:left;font-size: 20px;margin-top: 3px;cursor: pointer"></svgIcon>
                </div>
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          v-for="(item, index) in tableTitle"
          :key="index"
          :prop="item.prop"
          :label="item.label"
          :width="item.width">
        </el-table-column>
        <el-table-column
          label="操作">
          <template slot-scope="scope">
            <div style="float: left; margin-right: 30px">
              <SvgIcon icon-class="iconyulan"></SvgIcon>
              <el-button type="text" size="small" @click="view">预览</el-button>
            </div>
            <div style="float: left">
              <SvgIcon icon-class="iconxiazaizhengshu"></SvgIcon>
              <el-button type="text" size="small">下载文件</el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
export default {
  name: 'improvement',
  data() {
    return {
      model: '',
      options: [],
      tableTitle: [
        {
          label: '文件名称',
          prop: 'fileTitle'
        },
        {
          label: '发布时间',
          prop: 'time'
        },
        {
          label: '下载次数',
          prop: 'count'
        }
      ],
      tableData: [
        {
          fileTitle: '教学工作规程',
          time: '2018-2019学年  第二学期',
          count: '36次'
        },
        {
          fileTitle: '“十三五”教育事业发展规划',
          time: '2015-2016学年  第一学期',
          count: '124次'
        },
        {
          fileTitle: '本科专业人才培养通用标准',
          time: '2016-2017学年  第一学期',
          count: '52次'
        },
        {
          fileTitle: '学委员会规程（试行）',
          time: '2017-2018学年  第二学期',
          count: '64次'
        },
        {
          fileTitle: '本科生学分制暂行规定',
          time: '2016-2017学年  第一学期',
          count: '68次'
        },
        {
          fileTitle: '教学二级管理实施办法(试行)',
          time: '2016-2017学年  第一学期',
          count: '11次'
        },
        {
          fileTitle: '深化本科教育教学综合改革的实施意见',
          time: '2016-2017学年  第一学期',
          count: '11次'
        },
        {
          fileTitle: '本科专业培养目标合理性评价办法（试行）',
          time: '2016-2017学年  第一学期',
          count: '11次'
        },
        {
          fileTitle: '深化本科生创新创业教育改革工作实施方案',
          time: '2016-2017学年  第一学期',
          count: '11次'
        },
        {
          fileTitle: '实验室安全管理办法',
          time: '2016-2017学年  第一学期',
          count: '11次'
        },
        {
          fileTitle: '实验室工作条例',
          time: '2016-2017学年  第一学期',
          count: '11次'
        }
      ]
    }
  },
  methods: {
    view() {
      window.open(process.env.BASE_URL + '/image/2019/12/03/120498589079023.pdf')
    }
  }
}
</script>
<style lang="scss">
.improvementFile {
  // padding-top: 30px;
  .el-table__expanded-cell[class*=cell] {
    background-color: #fcfcfc;
  }
  .tableTop {
    overflow: hidden;
    // margin-bottom: 30px;
    .tableTitle {
      // width:140px;
      // height:40px;
      // background:rgba(3,177,135,1);
      // box-shadow:0px 2px 20px 0px rgba(3,177,135,0.3);
      // border-radius:20px;
      // color: #fff;
      // letter-spacing: 0.1em;
      // text-align: center;
      // line-height: 40px;
      float: left;
    }
    .el-select {
      float: right;
      margin-top: 22px;
    }
  }
  .table {
    padding-bottom: 50px;
    background-color: #fff;
    border-radius: 10px;
    margin-bottom: 40px;
  }
}
</style>


